.contact-contain {
  grid-template-columns: repeat(2, max-content);
  justify-content: center;
  column-gap: 6rem;
  padding-bottom: 3rem;
}

.contact-title {
  text-align: center;
  font-size: var(--h3-font-size);
  font-weight: var(--font-medium);
  margin-bottom: var(--mb-1-5);
}

.contact-info {
  display: grid;
  row-gap: 1rem;
  grid-template-columns: 300px;
}

.contact-card {
  background-color: var(--container-color);
  border: 1px solid rgba(0, 0, 0, 0.168);
  border-radius: 0.755rem;
  padding: 1rem;
  text-align: center;
}

.contact-card-icon {
  font-size: 2rem;
  color: var(--title-color);
  margin-bottom: var(--mb-0-25);
}

.contact-card-title,
.contact-card-data {
  font-size: var(--small-font-size);
}

.contact-card-title {
  font-weight: var(--font-medium);
}

.contact-card-data {
  display: block;
  margin-bottom: var(--mb-0-75);
}

.contact-button {
  color: var(--text-color);
  font-size: var(--small-font-size);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  column-gap: 0.25rem;
}

.contact-button-icon {
  font-size: 1.3rem;
  transition: 0.3s;
}

.contact-button:hover .contact-button-icon {
  transform: translate(0.25rem);
  color: var(--main-blue);
}

.contact-button:hover {
  color: var(--main-blue);
}

.contact-form {
  width: 360px;
}

.contact-form-div {
  position: relative;
  margin-bottom: var(--mb-2);
  height: 2.5rem;
}

.contact-form-input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 2px solid rgba(0, 0, 0, 0.179);
  background: none;
  color: var(--text-color);
  outline: none;
  border-radius: 0.75rem;
  padding: 2rem;
  z-index: 1;
}

.contact-form-tag {
  position: absolute;
  top: -0.75rem;
  left: 1.25rem;
  font-size: var(--smaller-font-size);
  padding: 0.25rem;
  background-color: var(--body-color);
  z-index: 10;
}

.contact-form-area {
  height: 12rem;
}

.contact-form-area textarea {
  resize: none;
}

@media screen and (max-width: 992px) {
  .contact-contain {
    column-gap: 3rem;
  }
}

@media screen and (max-width: 768px) {
  .contact-contain {
    grid-template-columns: 1fr;
    row-gap: 3rem;
  }

  .contact-info {
    justify-content: center;
  }

  .contact-form {
    margin: 0 auto;
  }
}

@media screen and (max-width: 576px) {
  .contact-info {
    grid-template-columns: 1fr;
  }

  .contact-form {
    width: 100%;
  }
}

